@import url("https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap");
/*BOXED LAYOUT*/
/*GRID - media queries breakpoints*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #17181a;
  height: 100vh;
  line-height: 1.5;
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
  position: relative;
}

main {
  position: absolute;
}

#logo {
  font-family: "Abril Fatface";
  color: white;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.container-fluid {
  height: 100%;
  width: 100%;
  padding-right: 30px;
  padding-left: 30px;
  margin-right: auto;
  margin-left: auto;
}
@media (max-width: 767px) {
  .container-fluid {
    padding-right: 15px;
    padding-left: 15px;
  }
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1024px;
  }
}
:root {
  --orange-cl: rgba(255, 139, 93);
  --violet-cl: rgba(167, 110, 231);
  --blue-cl: rgba(86, 124, 228);
  --black-cl: #17181a;
  --white-cl: #ffffff;
}

.nav-header {
  min-height: 10vh;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  width: 80%;
  margin: auto;
  padding: 1.25rem 0;
  z-index: 20;
  position: relative;
}

.line1,
.line2 {
  width: 3.125rem;
  height: 2px;
  margin-bottom: 6px;
  background-color: white;
  pointer-events: none;
}

.slide {
  position: relative;
  min-height: 100vh;
  margin: 0 auto;
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--white-cl);
  background-color: var(--black-cl);
}

.hero-img {
  width: 50%;
  position: relative;
  overflow: hidden;
}
.hero-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-desc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
  padding-left: 1.875rem;
  position: relative;
}
.hero-desc h2 {
  font-family: "Abril Fatface";
  font-size: 2.5rem;
  letter-spacing: 2px;
  font-weight: 500;
}
.hero-desc p {
  padding: 1.25rem 0;
  font-size: 1rem;
}
.hero-desc ul {
  margin-bottom: 1.25rem;
}
.hero-desc li {
  margin-left: 1.875rem;
}

.hyppe-span {
  color: var(--blue-cl);
}

.wooder-span {
  color: var(--violet-cl);
}

.natours-span {
  color: var(--orange-cl);
}

.explore {
  padding: 0.625rem 2.5rem;
  border: none;
  background: none;
  color: var(--white-cl);
  border: 1px solid var(--white-cl);
  text-decoration: none;
  font-size: 1rem;
  align-self: flex-start;
  cursor: pointer;
}

.nav-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--white-cl);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  clip-path: circle(50px at 100% -10%);
  -webkit-clip-path: circle(50px at 100% -10%);
  z-index: 1;
  opacity: 1;
}

.burger {
  cursor: pointer;
}

.nav-links {
  font-size: 2.5rem;
  padding: 0 12.5rem;
  cursor: pointer;
}
.nav-links h3 {
  padding: 1.25rem 0;
  transition: 0.3s;
}
.nav-links h3:hover {
  color: var(--blue-cl);
}

.contact {
  padding: 0 3.125rem;
}
.contact h2 {
  font-size: 2.5rem;
  padding: 1.25rem 0;
  color: var(--orange-cl);
}

.reveal-img,
.reveal-text {
  position: absolute;
  width: 100%;
  height: 150%;
  background-color: var(--black-cl);
  top: -15%;
  left: 0;
}

.cursor {
  width: 3.125rem;
  height: 3.125rem;
  border: 2px solid var(--white-cl);
  border-radius: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
  pointer-events: none;
  transition: all 0.3s ease-in-out;
  transition-property: background, transform;
  transform-origin: 75% 75%;
}

.cursor.nav-active {
  background-color: var(--blue-cl);
  transform: scale(2);
  z-index: 0;
}

.cursor.explore-active {
  background-color: var(--white-cl);
  transform: scale(2.5);
}

.scrollmagic-pin-spacer {
  position: static !important;
}

#logo {
  letter-spacing: 1.5px;
}

.cursor-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 0.5rem;
}

.title {
  position: relative;
  overflow: hidden;
}

.title-swipe {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  mix-blend-mode: darken;
  transform: translateY(100%);
}

.t-swipe1 {
  background-color: var(--blue-cl);
}

.t-swipe2 {
  background-color: var(--violet-cl);
}

.t-swipe3 {
  background-color: var(--orange-cl);
}

body.hide {
  overflow: hidden;
}

@media screen and (max-width: 1024px) {
  main {
    width: 100%;
    margin: 0;
  }

  .slide {
    display: block;
    width: 100%;
    text-align: center;
    padding: 1.875rem 0.9375rem;
  }

  .hero-img {
    width: 100%;
    margin: 0 auto;
    height: 40vh;
  }

  .hero-desc {
    justify-content: center;
    align-items: center;
    padding-left: 0;
  }
  .hero-desc h2 {
    margin-top: 20px;
  }
  .hero-desc .explore {
    width: 40%;
    margin: auto;
  }
  .hero-desc ul li {
    text-align: left;
  }

  .nav-bar {
    display: block;
  }

  .nav-links {
    text-align: center;
    margin-top: 100px;
    padding: 0;
  }

  .contact h2 {
    text-align: center;
  }
  .contact P {
    text-align: center;
  }
}
@media screen and (max-width: 768px) {
  .hero-desc h2 {
    font-size: 35px;
    margin-top: 10px;
  }
}
@media screen and (max-width: 576px) {
  .nav-header {
    width: 90%;
    padding: 0;
  }

  .hero-img {
    height: 30vh;
  }

  .hero-desc h2 {
    font-size: 30px;
    margin-top: 10px;
  }
  .hero-desc p {
    font-size: 0.9375rem;
  }
  .hero-desc ul {
    font-size: 0.9375rem;
  }
}
.fashion1,
.fashion2,
.fashion3 {
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  color: white;
  margin-top: 50px;
  position: relative;
}

.fashion-img {
  height: 80vh;
}
.fashion-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.fashion-text {
  width: 50%;
}
.fashion-text h1 {
  font-size: 3.75rem;
  font-weight: 400;
  padding: 30px 0;
}
.fashion-text p {
  font-size: 1rem;
  padding-right: 20px;
}

.fashion1 h1 {
  color: #ff8b5d;
}

.fashion2 h1 {
  color: #a76ee7;
}

.fashion3 h1 {
  color: #567ce4;
}

.fashion-nr {
  font-size: 200px;
  position: absolute;
  left: 40%;
  bottom: 0;
  color: white;
  opacity: 0.2;
}

.swipe {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.swipe1 {
  background: #a76ee7;
  z-index: 5;
}

.swipe2 {
  background: #567ce4;
  z-index: 4;
}

.swipe3 {
  background: #ff8b5d;
  z-index: 3;
}

@media screen and (max-width: 1024px) {
  .detail-slide {
    width: 100%;
    padding: 0 15px;
  }

  .fashion-nr {
    font-size: 180px;
    left: 30%;
  }
}
@media screen and (max-width: 768px) {
  .detail-slide {
    flex-direction: column;
  }

  .fashion-img {
    height: 68vh;
  }

  .fashion-text {
    text-align: center;
    width: 100%;
  }
  .fashion-text h1 {
    font-size: 50px;
    padding-bottom: 0;
  }
  .fashion-text p {
    padding: 20px 0;
  }

  .fashion-nr {
    left: 70%;
    top: 0;
  }
}